import { Component } from '@angular/core';
import { ViewController,IonicPage,  NavController,Events , ActionSheetController, NavParams } from 'ionic-angular';
import {   FormBuilder,  FormGroup,   Validators } from '@angular/forms';
import { Auth,NotificationProvider } from '../../../../providers';
import { TranslateService } from '@ngx-translate/core';
import { Storage } from '@ionic/storage';


@IonicPage()
@Component({
  selector: 'page-Unidadacademica3-create',
  templateUrl: 'unidadacademica3-create.html',
})
export class Unidadacademica3CreatePage {


  public form                  : FormGroup;
  username: string = "";
  public idtipo: string="";
  public idtiponombre: string="";
  public nombre                 : any;
  public ididioma                 : any;
  public idtipocurso                 : any;
  public idtipogrupo                 : any;

  public estado                 : any;
  public codigo		     : any;
  public ididiomatt		     : any;
  public idtipocursott		     : any;
  public idtipogrupott		     : any;
  public vgrupo  :any=[];

  public vtipogrupo  :any=[];
  public vtipocurso  :any=[];

  public aext2		     : any;
  public aest		     : any;
  public atrab		     : any;
  public ahijotrab1		     : any;
  public ahijotrab2		     : any;
  public apoblacion		     : any;
  public aadol		     : any;
  public aext		     : any;
  idempresa:any;
  private _ID                  : String;
  public pageTitle           	: string;

  constructor( public storage: Storage,public msj: NotificationProvider, public translateService: TranslateService,public viewCtrl: ViewController,public authService:  Auth,public navCtrl 		: NavController,
    public navParams 	: NavParams,   private _FB          : FormBuilder,         public actionSheetCtrl: ActionSheetController,
          public events: Events)
  {
    this.translateService.get('APPX').subscribe((value) => {
      this.idempresa=value
    });
     this.form = this._FB.group({
       nombre     : ['', Validators.required],
       ididioma     : ['', Validators.required],
       idtipocurso     : ['', Validators.required],
       idtipogrupo     : ['', Validators.required],
       codigo    : [''],
       estado    : [''],
       aest    : [''],
       aext2    : [''],
       atrab    : [''],
       ahijotrab1    : [''],
       ahijotrab2    : [''],
       apoblacion    : [''],
       aadol    : [''],
       aext    : ['']


     });





  }




limpia(op:string) : void
{

  this.form.controls['aest'].setValue(op);
  this.form.controls['aext2'].setValue(op);
   this.form.controls['atrab'].setValue(op);
   this.form.controls['ahijotrab1'].setValue(op);
   this.form.controls['ahijotrab2'].setValue(op);
   this.form.controls['apoblacion'].setValue(op);
  this.form.controls['aadol'].setValue(op);
  this.form.controls['aext'].setValue(op);
}


  ngOnInit() {
    this.storage.get('username').then((value) => {
      this.username = value;
  });
  }

  ionViewDidLoad() : void
  {
    this.idtipo=this.navParams.data.record2.idtipo;
    this.idtiponombre=this.navParams.data.record2.ntipo;



  this.authService.getReg(this.idtipo+'/'+ this.idtipo,'unidadidioma3s').then((result) => {
    this.vgrupo=result;
  }, (err) => {
  this.msj.showError3('Error',err.error);
});


this.authService.getReg(this.idtipo+'/'+ this.idtipo,'unidadtipocurso3s').then((result) => {
  this.vtipocurso=result;
}, (err) => {
this.msj.showError3('Error',err.error);
});



this.authService.getReg(this.idtipo+'/'+ this.idtipo,'unidadtipogrupo3s').then((result) => {
  this.vtipogrupo=result;
}, (err) => {
this.msj.showError3('Error',err.error);
});

this.limpia('false');

    if(this.navParams.get("record")==null)
    {


      this.translateService.get('ITEM_CREATE_TITLE').subscribe((value) => {
        this.pageTitle=value;
      });
    }
    else {
       this._ID 				=	this.navParams.data.record._id;
       this.nombre		=	this.navParams.data.record.nombre;
       this.codigo		=	this.navParams.data.record.codigo;
       this.estado		=	this.navParams.data.record.estado;
       this.ididioma=	this.navParams.data.record.ididioma._id //+','+this.navParams.data.record.ididioma.nombre;
       this.idtipocurso=	this.navParams.data.record.idtipocurso._id //+','+this.navParams.data.record.ididioma.nombre;
       this.idtipogrupo=	this.navParams.data.record.idtipogrupo._id //+','+this.navParams.data.record.ididioma.nombre;
    this.ididiomatt=this.ididioma
this.idtipocursott= this.idtipocurso
this.idtipogrupott =this.idtipogrupo
       this.form.controls['aest'].setValue(	this.navParams.data.record.aest);
       this.form.controls['aext2'].setValue(	this.navParams.data.record.aext2);
       this.form.controls['atrab'].setValue(	this.navParams.data.record.atrab);
       this.form.controls['ahijotrab1'].setValue(	this.navParams.data.record.ahijotrab1);
       this.form.controls['ahijotrab2'].setValue(	this.navParams.data.record.ahijotrab2);
       this.form.controls['apoblacion'].setValue(this.navParams.data.record.apoblacion);
      this.form.controls['aadol'].setValue(this.navParams.data.record.aadol);
      this.form.controls['aext'].setValue(	this.navParams.data.record.aext);



       this.translateService.get('ITEM_UPDATE_TITLE').subscribe((value) => {
        this.pageTitle=value;
      });
    }
  }



  manageGallery() : void
  {
  let creat='Actualiza';
    if(this.navParams.get("record")==null)
    {  creat='Crea';
    }
     let   options       : any	     = {
      idtipounidad : this.idtipo,
     nombre : this.form.controls['nombre'].value,
     codigo: this.form.controls['codigo'].value,
     estado: this.form.controls['estado'].value,
     ididioma:this.ididiomatt,
     idtipocurso:this.idtipocursott,
     idtipogrupo:this.idtipogrupott,
     aest:this.aest,
     aext2:this.aext2,
     atrab:this.atrab,
     ahijotrab1:this.ahijotrab1 ,
     ahijotrab2:this.ahijotrab2 ,
     apoblacion:this.apoblacion ,
     aadol:this.aadol  ,
     aext:this.aext   ,

     bitacora:{idempresa:this.idempresa,idafiliado:'',email: this.username,permiso:creat,accion:creat+' unidad acedemica '}
    };


     if(this.navParams.get("record")==null)
     {
          if(options){
            this.authService.createReg('',options,'unidadacademica3s').then((result) => {
              if (!this.form.valid) { return; }
              this.viewCtrl.dismiss(result);
          }, (err) => {
            this.msj.showError3('Error',err.error);
          });
          }
     }
     else
     {
      if(options){
        this.authService.createReg(this._ID,options,'unidadacademica3s').then((result) => {
           if (!this.form.valid) { return; }
           console.log(result)
           this.viewCtrl.dismiss(result);
       }, (err) => {
        this.msj.showError3('Error',err.error);
       });
    }}
  }

  cancel() {
    this.viewCtrl.dismiss();
  }
  done() {
    if (!this.form.valid) { return; }
    //this.viewCtrl.dismiss(this.form.value);
  }


  onSelectChangeB(selectedValue: any) {   this.ididiomatt=selectedValue; }

  onSelectChangeC(selectedValue: any) {   this.idtipocursott=selectedValue; }
  onSelectChangeD(selectedValue: any) {   this.idtipogrupott=selectedValue; }


}



